import React from 'react';
import PaginatedItems from '../components/PaginatedItems';
const Home = () => {
  return (
    <div
      style={{
        display: 'flex',
        justifyContent: 'center',
        marginTop:'1rem',
        height: '90vh'
      }}
    >
      
      <PaginatedItems itemsPerPage={1} />
  
    </div>
  );
};

export default Home;